export function createUser(nanmeValue, emailValue, messageValue) {
  return db
    .collection("User Contact")
    .add({
      name: nanmeValue,
      email: emailValue,
      message: messageValue
    })
    .then(function(docRef) {})
    .catch(function(error) {});
}

